












import { Component, Prop, Vue } from 'vue-property-decorator';
import { Place } from '@/Dto';
import { Util } from '@/Util';
import Tags from '../Tags.vue';

@Component({
  name: 'panel-place-list',
  components: {
      Tags,
  }
})
export default class PanelPlacesList extends Vue {
  @Prop()
  private places!:Array<Place>;

  address(place: Place) {
    return place.address.number + ' ' + place.address.street + ', ' + place.address.suburb;
  }
}
